import { FunctionalComponent, h } from '@stencil/core';
import { ButtonIconPosition } from 'components/button/button.model';
import { IconSizeTypes, IconType, transformIconSize } from '@utils/helper';
import { IconColorTypes } from 'components/icon/icon.model';

export type FcButtonCompType = {
  color: IconColorTypes;
  size: IconSizeTypes;
  icon?: string;
  iconPosition?: ButtonIconPosition;
  disabled?: boolean;
  readonly?: boolean;
  buttonAriaLabel?: string;
  tabindex?: number;
  onClick?: () => void;
  onFocus?: () => void;
  onBlur?: () => void;
};

export const FcButtonComp: FunctionalComponent<FcButtonCompType> = props => {
  const getButtonClass = () => ({
    'ath-button_comp': true,
    [`ath-button_comp--size-${props.size}`]: !!props.size,
    'ath-button_comp--disabled': !!props.disabled,
  });

  const handleClick = (e: Event) => {
    if (!props.disabled && !props.readonly && props.onClick) {
      e.stopPropagation();
      props.onClick();
    }
  };

  const buttonTabIndex = props.disabled ? '-1' : (props.tabindex ?? '0');

  return (
    <button
      class={getButtonClass()}
      disabled={props.disabled}
      aria-label={props.buttonAriaLabel}
      onClick={handleClick}
      onFocus={props.onFocus}
      onBlur={props.onBlur}
      tabIndex={Number(buttonTabIndex)}
    >
      {props.icon && <ath-icon icon={props.icon} size={transformIconSize(IconType.ButtonComp, props.size)} color={props.disabled ? 'disabled' : props.color}></ath-icon>}
    </button>
  );
};
